    p.mt-5 La composicin grfica se refiere a la organizacin o disposicin de elementos dentro del espacio visual, con el objeto de construir una imagen que transmita una idea de manera sencilla y directa a un pblico objetivo. Esta premisa sigue teniendo la misma validez para cuando se trata de la creacin y/o edicin de imgenes fijas, pero tiene una definicin distinta cuando se trata de imgenes en movimiento. 
