        p Es comn hablar de edicin y montaje como el mismo proceso tcnico, ya que la palabra montaje, del francs #[i montage], hace referencia a montar, poner encima; pero para los tericos del lenguaje audiovisual, el montaje tiene que ver con un proceso narrativo, mientras que la edicin contempla un proceso de tipo tcnico. De esta manera, lo que se refiere a la composicin de mensajes o significados a travs de elementos audiovisuales se conoce como montaje audiovisual, que es el paso tcnico y narrativo ltimo del producto audiovisual, aunque viene definido previamente en la elaboracin del guion.
        p.mt-3 En todo caso, para desarrollar los temas necesarios en este apartado, se deben considerar las dos definiciones, porque como es sabido, en lo que respecta al tratamiento de imgenes fijas, lo comn es hablar de edicin de imgenes, mientras que montaje fotogrfico o fotomontaje se refiere a un producto de la edicin. 
    p.mt-4 Por consiguiente, a continuacin, se hace un acercamiento frente a las definiciones: 
