      h2 1.1  Edicin de imagen

    p Cuando se habla de edicin digital se involucran en el proceso una imagen, un terminal computarizado (equipo de escritorio, porttil, <i>tablet</i>, celular, dispositivo mvil) y un software especializado para tal fin.
