                        h4.mb-0
                          spam.bg--4.p-1
                            | La imagen digital
                      p Segn la 
                        b (Real Academia Espaola, s. f.) 
                        |una imagen es toda forma o figura percibida por el sentido de la vista. 

                        br
                        br
                        |Se define que como una 
                        b imagen digital o grfico digital, 
                        |la cual es representada de manera bidimensional, a partir de una imagen de una matriz numrica, frecuentemente en binario unos y ceros; esta se almacena en una memoria informtica para luego ser interpretada por un sistema informtico y que se transforma en una imagen visible a travs de la pantalla e imprimible a travs de cualquier dispositivo de salida.
          .py-3.py-md-4(titulo="Tipos de imgenes digitales")
            .titulo-sexto.color-primario(data-aos="fade-right")
              h4.mb-0
                spam.bg--4.p-1
                  | Tipos de imgenes digitales
            p Por su estructura, estas pueden ser de dos tipos:
            SlyderE(indicadores="centro")
              .row
                .col-lg-8
                  h4 Imgenes en mapa de bits
                  p Son las captadas del mundo real a travs de dispositivos digitales; segn Ordez (2005):
                    br
                    br
                    |Es una #[b estructura de datos que representa una rejilla rectangular de pxeles o puntos de color] conocida como <i>raster</i>, la cual puede visualizarse en un monitor de computadora, papel u otros dispositivos de representacin. Este tipo de imgenes se obtiene de medios como el escner o las cmaras digitales; su representacin en pantalla es a travs de la unidad mnima denominada pxel, que proviene del ingls <i>picture element</i>. Un pxel es la menor unidad en que se descompone una imagen digital, ya sea una fotografa, fotograma de video o grfico. Un pxel es un concepto inmaterial que no tiene una medida concreta.
                .col-lg-4: img(src="@/assets/curso/16.jpg")
              .row
                .col-lg-8
                  h4 Imgenes vectoriales
                  p Son las creadas desde su inicio a travs de un <i>software</i> que, mediante clculos matemticos y coordenadas sobre los ejes X y Y en la pantalla, describen posiciones de puntos trazados lneas curvas o rectas que se generan como objetos independientes, por lo que despus se pueden modificar fcilmente sin que por ello los dems componentes del dibujo se vean modificados. En los mapas de bits lo que ocurre es que, al incluir un elemento en la imagen, este queda automticamente incrustado y no puede volver a editarse de manera independiente. Las vectoriales no necesitan mucho espacio de almacenamiento y son independientes de la resolucin, ya que no dependen de una retcula determinada. 
                    br
                    br
                    |Pese a que presentan caractersticas distintas, cada da es ms comn que ambos tipos de imgenes se vinculen a travs de programas de tratamiento de imgenes.
                .col-lg-4: img(src="@/assets/curso/17.jpg")
          .py-3.py-md-4(titulo="Elementos de la imagen digital")
