                      .bloque-texto-g__texto.p-4
                        .textos.d-block 
                          h4 Elementos de la imagen digital
                          p.mb-0 Una vez definidos los tipos de imagen digital, a continuacin se presentan algunos elementos propios del universo de la imagen digital, que ayudan a la comprensin de su construccin y posterior edicin.
          .py-3.py-md-4(titulo="Formatos")
            SlyderE(indicadores="centro").mb-5
              div
                .tarjeta.tarjeta--azul.p-4.mb-5.mb-lg-5
                  .titulo-sexto.color-primario(data-aos="fade-right")
                    h4.mb-0
                      spam.bg--4.p-1
                        | Formatos
                  p Se refieren a la forma codificada que adquieren las imgenes al ser almacenadas en un dispositivo. En realidad se trata de cmo se almacenan digitalmente los pxeles que componen las imgenes digitales de mapa de bits o vectores. Cada formato cuenta con su propia metodologa para almacenar informacin de la imagen digital y cada uno tiene fortalezas, pero tambin desventajas. El formato depende de las caractersticas, la calidad y el uso final del nuevo archivo. 
                .titulo-sexto.color-primario.mt-5(data-aos="fade-right")
                  h4.mb-0
                    spam.bg--4.p-1
                      | Formatos para mapa de bits 
                .tarjeta.bg--1.p-4
                  SlyderA
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/19.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white BMP: desarrollado para sistemas operativos Microsoft como formato de almacenamiento para mapas de bits con una profundidad de color de hasta 24 bits por pxel. Los archivos en este formato son relativamente grandes, por lo que no es adecuado para ser usado en pginas <i>web</i>.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/20.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white TIFF: formato que se utiliza para la transmisin de datos impresos y de imgenes de alta resolucin; soporta el modelo de color CMYK y permite una profundidad de color de hasta 16 bits. Es el formato estndar cuando la calidad prima sobre el tamao de los archivos. Es empleado por las editoriales y los medios impresos.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/21.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white JPEG: sistema de compresin con prdida, utilizado para lograr que las imgenes digitales que se utilizan sean lo ms pequeas posible y se carguen rpidamente en el documento.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/22.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white GIF: es el formato preferido en Internet gracias a su posibilidad de animar imgenes. Los archivos GIF utilizan mapas de bits y admiten hasta 8 bits/pxel, permitiendo 256 colores en la gama RGB. 
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/23.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white PSD: del ingls Photoshop Document, es un formato nativo de proyectos grficos desarrollados con Photoshop. Guarda la informacin relacionada con las capas, los canales, la historia, lo que permite la posterior edicin de los mismos. Las imgenes en formato PSD solo pueden abrirse y editarse con Adobe Photoshop. Las imgenes resultantes son de tipo pxel. 
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/24.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white RAW: solo se encuentra disponible en cmaras digitales sofisticadas, indicadas para fotgrafos profesionales. Este formato ofrece la mxima calidad ya que contiene los pxeles en bruto tal y como se han adquirido, generando un archivo que contiene la totalidad de los datos de la imagen, por lo que comnmente se lo conoce como negativo digital. Los datos del archivo RAW generan un tipo de compresin sin prdida de informacin, por lo que comnmente suele decirse que no comprimen. 
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/25.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white PDF: del ingls Portable Document Format. Es un tipo de archivo universal, que funciona independientemente del <i>software</i>, del sistema operativo, de la resolucin, de la versin, entre otros; usados para la creacin del archivo original. Este formato permite el almacenamiento de archivos bitmap, vector, texto, adicionalmente soporta archivos que pueden tener diversas pginas.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/26.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white PNG: este formato se destaca porque permite comprimir imgenes sin prdidas de calidad.  Adems, ofrece la posibilidad de almacenar y publicar imgenes y grficos respetando las transparencias. 
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/27.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white EPS: del ingls Encapsulated PostScript, es un formato estandarizado que permite importar y exportar archivos de texto e imgenes, es compatible con todos los sistemas operativos. Se usa bsicamente en el mbito editorial.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/28.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white AI: es la extensin nativa de los archivos generados desde Adobe Illustrator, que se clasifican como archivos de imgenes vectoriales. Las imgenes vectoriales son las ms indicadas para usar en pginas <i>web</i> por el peso final de ellas. Una ventaja de la imagen vectorial es que se pueden escalar sin que pierdan calidad, al tiempo que requiere menos espacio de almacenamiento.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/29.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white SVG: del ingls Scalable Vector Graphics. Al igual que el formato Ai, el SVG es una extensin de grficos vectoriales bidimensionales, tanto estticos como animados. Es un formato poco conocido pero muy til para ser utilizarlo en entornos digitales. Su ventaja radica en que es un formato abierto y estndar.
                    .row.p-4.align-items-center.justify-content-center
                      .col-md-2
                        figure
                          img(src="@/assets/curso/30.svg")           
                      .col-md-8.mb-4.mb-md-0
                        p.text-white CDR: es la extensin nativa de los archivos generados desde Corel Draw. Al igual que los archivos Ai, las imgenes resultantes de Corel se clasifican como archivos de imgenes vectoriales. Se puede decir que Corel Draw es la competencia directa de Illustrator.
              div
                .tarjeta.color-primario.p-3.mb-5
                  .row.justify-content-around.align-items-center
                    .col-3.col-sm-2.col-lg-2
                      img(src="@/assets/curso/31.svg")
