          .py-3.py-md-4(titulo="Resolucin de la imagen")
            SlyderE(indicadores="centro").mb-5
              div
                .tarjeta.tarjeta--azul.p-4.mb-5.mb-lg-5
                      .titulo-sexto.color-primario(data-aos="fade-right")
                        h4.mb-0
                          spam.bg--4.p-1
                            | Resolucin de la imagen
                      p Desde una visin general, segn Fernndez (2015):
                        br
                        br
                        |La resolucin se refiere a la capacidad de una tecnologa o un mecanismo para reflejar los detalles de una imagen. A mayor resolucin, mejor calidad de imagen, pero tambin ms tamao y peso. 
                        br
                        |Si se divide una imagen en pocos pxeles, se podr codificar con poca informacin, pero seguramente perder mucho detalle, por lo que se dice que tiene poca resolucin. Si se divide en muchas ms partes, estas llegarn a ser tan pequeas que no se podrn  distinguir. La visin de la imagen ser mucho mejor y ms detallada, pero tambin mucho ms costosa en bits al ser ms grande y ms pesada. 
                .row.p-4.align-items-center.justify-content-center
                  .col-lg-10
                    figure
                      img(src="@/assets/curso/32.jpg")
              div
                .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-3.mb-5
                  .bloque-texto-g__img(
                    :style="{'background-image': `url(${require('@/assets/curso/33.jpg')})`}"
                  )
                  .bloque-texto-g__texto.p-4.w-90.text-small
                    h4.d-block Resolucin de la imagen
                    p.mb-0 El tamao y la frecuencia de los pxeles siempre son eleccin del diseador, quien deber tener en cuenta que el concepto de resolucin hace referencia a tres tipos de tamao:  en pxeles, el informtico (pantalla) y el superficial. El tamao en pxeles habla, como ya se ha dicho, de en cuntas filas y columnas se divide la imagen, o de cul es el nmero total de pxeles. 
                      br
                      br
                      |El tamao informtico se cuenta en unidades de informacin como <i>bytes</i>, <i>kilobytes</i> o <i>megabytes</i>, y depende del nmero de pxeles y de la cantidad de <i>bytes</i> que gastamos para definir cada pxel. Tamao de salida es lo que ocupar la foto si se imprime; como los pxeles son informacin numrica, es el editor quien decide, cuntos pxeles quiere imprimir en cada centmetro o pulgada de papel.
                      br
                      br
                      |En conclusin, tal y como lo expresa Fernndez (2005):
                .row.align-items-center
                  .col-5.col-lg-2.col-md-3.col-sm-4: img(src="@/assets/curso/34.svg")
                  .col-7.col-lg-10.col-md-9.col-sm-8
                    .bloque-texto-b.color-secundario.p-4
                      .bloque-texto-b__texto
                        i.fas.fa-quote-left
                        h5.mb-0 La resolucin de una imagen digital se expresa multiplicando su anchura por la altura en pxeles. Por ejemplo la imagen de 1200 x 1200 pxeles = 1.440.000 pxeles, expresado en Mp (megapxel) es igual a 1,4 Mp. Conviene tener en cuenta que 1 Megapxeles = 1024 pxeles.
                        i.fas.fa-quote-right                       
          .py-3.py-md-4(titulo="Modos de color")
            SlyderE(indicadores="centro").mb-5
              div
                p.mb-5 Adems del formato y la resolucin, otro elemento imprescindible en la imagen es el color.
                .row.justify-content-center
                  .col-lg-10
                    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-3.mb-5
                      .bloque-texto-g__img(
                        :style="{'background-image': `url(${require('@/assets/curso/35.jpg')})`}"
                      )
                      .bloque-texto-g__texto.p-4.w-90.text-small
                        h4.d-block Modos de color
                        p.mb-0 El color puede entenderse en trminos tcnicos como la profundidad de color en la imagen, es decir, la cantidad de bits de informacin necesarios para representar el color de un pxel en una imagen digital.  En trminos generales se alude al concepto de modelo de color en la imagen, que describe los colores que pueden verse en las imgenes digitales e impresas. Se pueden diferenciar tres modelos de color para trabajar en multimedia: modelo RGB, modelo CMYK y modelo HSB.
              div 
                .row.mt-3
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-32.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Color CMYK:] se refiere a los colores Cian, Magenta, Yellow y Black, que son los colores que se usan en impresin.
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-33.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Color RGB:]  Red, Green, Blue. Este es el espacio de color que usan los monitores para presentar este tipo de imagen tricromtica.
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-34.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Escala de grises:] este modo de color presenta una imagen monocromtica que maneja un espacio de colores limitado a 256 porcentajes de negro.
                .row.mt-3
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-35.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Color Lab:] consiste en tres canales: un canal de luminosidad (L) y dos canales cromticos, A (que oscila entre verde y rojo) y B (que oscila entre azul y amarillo). #[strong (M, 2014).]
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-36.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Mapa de bits:] el modo Mapa de bits utiliza uno de los dos valores de color (blanco o negro) para representar los pxeles de una imagen. Las imgenes en modo Mapa de bits se denominan imgenes monocromticas.
                  .col-4
                    figure
                      img(src="@/assets/template/tema-1-37.png", alt="Texto que describa la imagen")
                    p.mt-4 #[strong Color indexado:] en este modo el nmero de colores mximo es de 256. #[strong (M, 2014).]
    p.mb-5 Ya vistos los temas que conciernen a la imagen digital, es momento de pasar a la edicin de la misma. Para iniciar, una breve definicin:
    Separador
    .titulo-sexto.color-primario(data-aos="fade-right")
      h4.mb-0
        spam.bg--4.p-1
          | Definicin de edicin de la imagen
    .contenedor-jux.d-flex.justify-content-center
      <iframe frameborder="0" class="juxtapose" width="80%" height="722" src="https://cdn.knightlab.com/libs/juxtapose/latest/embed/index.html?uid=dfb199c6-11d7-11ec-abb7-b9a7ff2ee17c"></iframe>
    p.mb-5 Es la manipulacin, a travs de programas de edicin grfica, de imgenes capturadas o previamente diseadas, con la intencin de reforzar conceptos o mensajes mediante la modificacin del color, formas, composicin, contrastes, estilo, etctera. La edicin para hacer <i>collages</i> o mejorar la calidad de la imagen, es tambin un recurso de gran valor en publicidad; gracias a ello, se pueden exagerar o disminuir rasgos o elementos de todo tipo (Viveros, 2005) El programa donde se hace el tratamiento es un editor de imgenes que ofrece opciones de edicin, herramientas, efectos y modificacin en el formato al guardar el archivo.
    Separador
    .titulo-sexto.color-primario(data-aos="fade-right")
      h4.mb-0
        spam.bg--4.p-1
          | Aspectos de la edicin de imgenes
    p.mb-5 El tratamiento o edicin de la imagen digital comprende aspectos creativos y herramientas tcnicas, explicados a continuacin:
    AcordeonB.mb-5(tipo="b" clase-tarjeta="tarjeta")(data-aos="zoom-in-up") 
      .row(titulo="Aspectos Creativos")
        .col-md-4
          figure
            img(src='@/assets/curso/36.jpg', alt='Texto que describa la imagen')
        .col-md-8.mb-4.mb-md-0
          p El proceso de edicin se refiere tambin a los aspectos conceptuales y creativos de los que se sirve quien realiza la edicin. Lo conceptual determina las caractersticas semiticas de los productos, como el sentido-significacin que se desea transmitir, la funcin que se espera realice, si acompaa a otros medios o tiene un rol principal, si es ilustrativa o descriptiva, etctera. Asimismo, es deseable determinar el estilo que se desea manejar y se relaciona con el realismo o la figuracin, entre otros aspectos (Viveros, 2005).
            br
            br
            |Estos aspectos fueron tratados ampliamente en el Componente Formativo Diseo para contenidos digitales, en el apartado de Diseo y se aplican de la misma manera para la creacin, captura o edicin de imgenes digitales. De esta manera, es necesario especificar la composicin adaptada a entornos digitales y el retoque como aspecto propio de este mundo.
      
      .row(titulo="Composicin Digital")
        .col-md-4
          figure
            img(src='@/assets/curso/37.jpg', alt='Texto que describa la imagen')
        .col-md-8.mb-4.mb-md-0
          p Tambin denominada composicin de efectos visuales, tiene que ver con la creacin de imgenes digitales a partir de manipular y combinar elementos de diferentes fuentes o herramientas digitales, con el fin de generar piezas homogneas que transmitan la sensacin de unidad en la imagen resultante, independiente de que sean reales o no. 
      
      .row(titulo="Retoque Digital")
        .col-md-4
          figure
            img(src='@/assets/curso/38.jpg', alt='Texto que describa la imagen')
        .col-md-8.mb-4.mb-md-0
          p Pese a ser un proceso tcnico que involucra el conocimiento de las herramientas del <i>software</i> que se use, es tambin un proceso creativo, puesto que el retoque puede llevar implcito significados que se quieren compartir con el receptor.  El retoque da la opcin de manipular imgenes o fotografas  y  permite alterar el escenario, la luz, el color y la percepcin de forma variada e ingeniosa para construir  una nueva imagen.
    Separador
    AcordeonC.mb-5(tipo="a" clase-tarjeta="tarjeta")(data-aos="zoom-in-up") 
      .row(titulo="Herramientas para la edicin")
        p.mb-5 Tienen que ver con los <i>software</i> de tratamiento de imagen. El mercado ofrece mltiples opciones y se clasifican entre los editores para computador, celulares y editores online. 
