      h2 1.2  Edicin de audio
    .row.mb-5
      .col-lg-6
        p Hace referencia a la posibilidad de manipular diferentes elementos sonoros, involucrando en el proceso un terminal computarizado y <i>software</i> especfico, con el fin de integrar o generar sonidos nuevos o que se adapten a una necesidad especfica.
          br
          br
          |Un sonido digital suele tener asociados una serie de condiciones y parmetros caractersticos entre los que se puede destacar: la tendencia a ser dinmicos, su capacidad de interactividad, su condicin ubicua y su naturaleza verstil.
      .col-lg-6: img(src="@/assets/curso/65.jpg")
    .titulo-sexto.color-primario(data-aos="fade-right")
      h4.mb-0
        spam.bg--4.p-1
          | Formatos para la edicin de audio 
    p.mb-5 Ahora es pertinente revisar los formatos de audio y conocer algunos <i>softwares</i> comnmente utilizados para la edicin de este tipo de recursos:
      br
      br
      | Segn el uso y la utilidad, los formatos de audio se dividen en dos categoras. Una, los que se usan para almacenar archivos de audio en DD, memorias USB o dispositivos que permiten escuchar este tipo de archivos (MP3, WAV, AIFF) y otra los que se usan en servicios de transmisin online (OGG, FLAC, ALAC).
    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.bg--2-claro.flechas-slyder.p-4(data-aos="zoom-in-up") 
          SlyderA
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/66.svg")           
              .col-md-8.mb-4.mb-md-0
                p MP3: formato de audio comprimido con prdida ms conocido. Al mismo tiempo es el ms compatible con mltiples reproductores multimedia. Se caracteriza por la compresin de la secuencia del sonido, dando como resultado un archivo realmente pequeo, que normalmente ocupa la mitad del archivo original. Para conseguirlo, es necesaria la prdida de determinados sonidos, que supuestamente no pueden ser escuchados por el odo humano.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/67.svg")           
              .col-md-8.mb-4.mb-md-0
                p WAV: formato sin compresin de datos creado por Microsoft e IBM. Es compatible con todos los programas reproductores de audio. Los archivos de formato WAV no son tan populares como los MP3 porque son de mayor tamao, aunque de mejor calidad.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/68.svg")           
              .col-md-8.mb-4.mb-md-0
                p AIFF: Audio Interchange File Format, formato estndar de audio, desarrollado por Apple para almacenar datos de sonido en computadores Apple. Hoy en da los archivos WAV y AIFF son compatibles.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/69.svg")           
              .col-md-8.mb-4.mb-md-0
                p WMA: del ingls Windows Media Audio, es un formato de archivos musicales creados por Microsoft. Una de sus principales caractersticas est en que aquellos archivos que han sido comprimidos en formato WMA ocupan menos espacio que los MP3, lo que implica tambin la reduccin de su calidad. Por tanto, se trata de un formato de compresin de audio con prdida.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/70.svg")           
              .col-md-8.mb-4.mb-md-0
                p FLAC: del ingls Free Lossless Audio Codec, es un formato de archivos de audio, libre y abierto, cuyo objetivo es reducir el tamao original sin perder calidad. FLAC se ha convertido en la competencia directa del formato WAV.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/71.svg")           
              .col-md-8.mb-4.mb-md-0
                p AAC: del ingls Advanced Audio Coding, es un formato de compresin con prdida que est usando Apple como formato principal para los iPods y para su <i>software</i> iTunes. Es la versin de Apple del formato MP3. (Truesdell, 2007, p 516).
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/72.svg")           
              .col-md-8.mb-4.mb-md-0
                p OGG: archivos de audio comprimidos, que por su tamao pueden ser transmitidos va WIFI. Adicionalmente puede contener video. No es compatible con dispositivos Apple o con sistema operativo iOS.
            .row.p-4.align-items-center.justify-content-center
              .col-md-2
                figure
                  img(src="@/assets/curso/73.svg")           
              .col-md-8.mb-4.mb-md-0
                p ALAC: del ingls Apple Lossless Audio Codec, es bsicamente la alternativa de Apple al formato FLAC. Es el formato utilizado por iTunes para las transmisiones de Apple Music.
    p.mb-5 Algunos programas para edicin de audio offline:
